$dark-red: #822000;
$paper: #f8f2d3;
$brown: #957a41;

$gray-lighter: #D2D2D2;
$gray-light: #7d7d7d;
$gray: #3f3f45;
$gray-dark: #2a2a2b;
$gray-darker: #212121;

input {
  font-family: "modesto-text", serif;
  background: none;
  border: none;
  font-size: 18px;
  // width: 100%;
  text-align: center;
  color: #000;

  @media (prefers-color-scheme: dark) {
    color: $gray-lighter;
  }
}

.form_container {
  // width: 100%;
  display: flex;
}

.field_container {
  flex-grow: 1;
}

.field_container label {
  display: block;
}

.form_container input {
  border: 2px solid #ccc;
  border-radius: 4px;
  margin-top: 5px;
  text-align: left;
  padding: 5px;
  width: 80%;
  background: #fff;

  @media (prefers-color-scheme: dark) {
    color: $gray-lighter;
    border: 2px solid $gray-light;
    background: $gray;
  }
}

input:focus {
  outline: $dark-red auto 5px;
  
  @media (prefers-color-scheme: dark) {
    outline: $gray-light auto 5px;
  }
}

.initiative_entry {
  display: grid;
  grid-template-columns: 25px auto 30px 30px;
  grid-template-rows: auto;
  border: 0;
  border-bottom: 1px solid $dark-red;
  // border-radius: 4px;
  padding: 5px 15px;
  margin-bottom: 1px;
  // background-color: #FDFDFD;

  @media (prefers-color-scheme: dark) {
    border-bottom: 1px solid $gray-light;
  }
}

.initiative_entry:focus {
  outline: $dark-red auto 5px;

  @media (prefers-color-scheme: dark) {
    outline: $gray-light auto 5px;
  }
}

.initiative_entry.is_dragging {
  background-color: #eee;

  @media (prefers-color-scheme: dark) {
    background-color: $gray;
  }
}

.initiative_entry__active {
  border: 3px solid blue;
}

.initiative_entry__active .hamburgerIcon {
  opacity: 100%;
}

.initiative_entry input {
  text-align: left;
}

.initiative_entry div {
  display: inline-block;
  overflow: hidden;
}

.initiative_entry .deleteIcon {
  display: none;
  float: right;
}

.initiative_entry:hover {
  background-color: $gray-light;

  .deleteIcon {
    display: block;
  }
}

.deleteIcon,
.hamburgerIcon,
.goIcon {
  height: 16px;
  width: 16px;
  opacity: 0.25;
}

.deleteIcon:hover {
  opacity: 0.5;
  cursor: pointer;
}

.hamburgerIcon {
  padding-top: 2px;

  @media (prefers-color-scheme: dark) {
    fill: $gray-lighter;
  }
}
